/* Material Input */
.materialInput{
    width: 90%;
    height: 30px;
    border-bottom: 2px solid #2874f0;
    position: relative;
    /* background-color: red; */
    margin-top: 20px;
  }
  .materialInput input{
    width: 100%;
    flex: 1 1;
    outline: none;
    border: none;
    z-index: 1;
    background: transparent;
    line-height: 30px;
  }
  .materialInput .label{
    position: absolute;
    line-height: 30px;
    color: grey;
    z-index: 0;
    font-size: 12px;
    letter-spacing: 1px;
    transition: all 0.1s ease-in-out;
  }
  .materialInput .label.focus{
    top: -20px !important;
  }

  /* Material Button */
.materialButton{
    width: 100%;
    display: inline-block;
    padding: 15px;
    font-size: 15px;
    outline: none;
    background-color: #fb641b;
    color: #ffffff;
    box-shadow: 0 1px 2px 0 rgba(0,0,0,.2);
    border: none;
    cursor: pointer;
    border-radius: 2px;
}
.materialButton:hover{
    box-shadow: 0 4px 6px 0 rgba(0,0,0,.12);
}

/* Material dropdown */
.headerDropdownContainer{
    position: relative;
    display: inline-block;
}
.firstmenu{
    padding: 15px 20px;
    border-bottom: 1px solid #eee;
    font-size: 12px;
    font-weight: 500;
    display: flex;
    justify-content: space-between;
  }
.dropdown{
  right: 50%;
  position: absolute;
  /* width: 200px; */
  margin-right: -60px;
  background: #fff;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
