  text-align: center;
  justify-content: center; */
  background-color: beige;
  margin:1.5vmax auto;
  display:flex ;
}




.productDetails {
  margin: 0 10px;
  display: inline-block;

}

.productDetails .productTitle {
  font-size: 40px;
  color: #33080A;
  font-weight: 600;
  margin-top: 2vmax;
}
.responsive-carousel-container{
  /* background-color: #388e3c; */
height: 32vmax;
width: 35vmax;
padding-top: 3vmax;

}

.ratingCount {
  display: inline-block;
  background: #388e3c;
  color: #fff;
  font-weight: 400;
  font-size: 12px;
  border-radius: 3px;
  padding: 2px 5px;
}
.carousal-container{
align-items: center;
  text-align: center;
  justify-content: center;
}
.ratingNumbersReviews {
  font-size: 12px;
  color: #777;
  font-weight: 600;
  display: inline-block;
  margin: 0 10px;
}

.extraOffer,
.discount {
  color: #ffffff;
  font-size: 12px;
  margin: 10px 0px;
  font-weight: 700;
  background-color: #f40000;
  padding: 0 .5vmax;
  -webkit-transform:skew(-10deg);
          transform:skew(-10deg);
}

.priceContainer {
  align-items: center;
  margin-top: -1vmax;
}

.price {
  font-size: 15px;
  font-weight: 600;
  display: flex;
  align-items: center;
}
.btns{
  padding-top: 10px;
}

@media  screen and (max-width: 770px) {
  .flexRow{
    flex-wrap: wrap;
    justify-content: center;

  }
  .descript{
    margin-left:8vmax ;
  }
}
@media  screen and (max-width: 770px) {
  .container{
    display: block;
    text-align: center;
    
  }
  .carousal-container{
    align-items: center;
    text-align: center;
    justify-content: center;
    display: flex;
  }
  
  .verticalImageStack {
    display: flex !important;
  }
  .thumbnail {
    width: 65px !important;
    height: 63px !important;
    border: 1px solid #f0f0f0 !important;
    overflow: hidden !important;
    text-align: center !important;
    padding-right: 2px !important;
  }
  
  .thumbnail:hover {
    transition: .8s ease !important;
    width: 80px !important;
    height: 63px!important;
    border: 1px solid #f0f0f0 !important;
    z-index: 1000 !important;
  }
  
  .thumbnail img {
    width: 605px !important;
    max-width: 100% !important;
    max-height: 100% !important;
    object-fit: contain !important;
  }
  
}
.cartItemContainer{
    margin: 10px 5px;
}
.cartProImgContainer{
    width: 112px;
    height: 112px;
    overflow: hidden;
    text-align: center;
}
.cartProImgContainer img{
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
}
.cartProImgContainer{
    width: 112px;
    height: 112px;
    overflow: hidden;
    text-align: center;
}
.cartProImgContainer img{
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
}
.cartItemDetails{
    display: flex;
    justify-content: space-between;
    flex: 1 1;
}

.cartActionBtn{
    margin: 0 10px;
    font-family: 'Roboto';
    font-weight: 600;
    text-transform: uppercase;
    background: transparent;
    border: none;
    color: #212121;
    cursor: pointer;
    font-size: 15px;
}
.quantityControl{
    display: flex;
    align-items: center;
}
.quantityControl button{
    width: 30px;
    height: 30px;
    border-radius: 15px;
    border: none;
    border: 1px solid #c2c2c2;
    background-color: transparent;
}
.quantityControl input{
    width: 40px;
    height: 20px;
    border: 1px solid #c2c2c2;
    margin: 0 5px;
    text-align: center;
}
.cartContainer{
    display: flex;
    max-width: 1260px;
    margin: 15px auto;
    justify-content: space-between;
}
.pdetails{
    visibility:hidden !important;
}
@media  screen and (max-width: 770px) {
    .pdetails{
        display: none !important;
    }
}
.checkoutContainer{
    width: calc(100% - 400px);
}
.checkoutStep{
    box-sizing: border-box;
    background: #fff;
    box-shadow: 0 1px 1px 0px rgba(0, 0, 0, 0.2);
    margin-bottom: 10px;
}
.checkoutHeader{
    width: 100%;
    box-sizing: border-box;
    padding: 10px 20px;
}
.checkoutHeader.active{
    background: #2874f0;
}
.checkoutHeader .stepNumber{
    display: inline-block;
    text-align: center;
    background: #eee;
    color: #2874f0;
    border-radius: 3px;
    font-size: 12px;
    box-sizing: border-box;
    padding: 3px 7px;
}
.checkoutHeader.active .stepNumber{
    background: #fff;
}
.stepTitle{
    display: inline-block;
    font-size: 14px;
    font-weight: 500;
    color: #878787;
    margin-left: 15px;
}
.checkoutHeader.active .stepTitle{
    color: #fff;
}
.loggedInId{
    font-size: 12px;
    margin: 0px 0 10px 60px;
}

.addressContainer{
    box-sizing: border-box;
    padding: 20px;
    background: #f5faff;
}
.addressinfo{
    padding-left: 15px;
    box-sizing: border-box;
    width: 100%;
}
.addressDetail{
    display: flex;
    justify-content: space-between;
    width: 100%;
    box-sizing: border-box;
    padding-bottom: 20px;
    font-weight: 600;
    font-family: 'Roboto';
    font-size: 12px;
    color: #333;
}
.addressType{
    display: inline-block;
    margin: 0 10px;
    text-transform: uppercase;
    background: #eee;
    font-weight: 500;
    padding: 1px 5px;
    font-size: 10px;
    border-radius: 2px;
    color: #777777;
}
.fullAddress{
    font-size: 12px;
    letter-spacing: 1px;
    line-height: 18px;
}
.stepCompleted{
    padding: 0 0 20px 60px;
    font-size: 12px;
}
.orderItemContainer{
    padding: 10px;
    box-sizing: border-box;
    display: flex;
}
.orderImgContainer{
    width: 80px;
    height: 80px;
    overflow: hidden;
    text-align: center;
}
.orderImg{
    max-width: 80px;
    max-height: 80px;
}
.orderRow{
    display: flex;
    flex: 1 1;
}
.orderName{
    width: 300px;
    box-sizing: border-box;
    padding: 0 20px;
    font-size: 12px;
}
.orderPrice{
    box-sizing: border-box;
    padding: 0 40px;
    display: flex;
    align-items: flex-start;
    font-size: 14px;
  }
.delAdrContainer{
    display: flex;
    box-sizing: border-box;
}
.delTitle{
    font-size: 15px;
    font-weight: 500;
    margin-bottom: 10px;
}
.delName{
    font-size: 12px;
    font-weight: 500;
}
.delAddress{
    font-size: 12px;
    margin: 10px 0;
}
.delPhoneNumber{
    font-size: 12px;
    font-weight: 500;
}
.delAdrDetails{
    padding: 20px;
    border-right: 1px solid #eee;
}
.delMoreActionContainer{
    padding: 20px;
}

.delItemImgContainer{
    width: 75px;
    height: 75px;
    overflow: hidden;
    text-align: center;
}
.delItemImgContainer > img{
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
}

.orderTrack{
    width: 400px;
    display: flex;
}
.orderStatus{
    width: 33.33%;
    height: 3px;
    background: #777;
    position: relative;
    font-size: 12px;
}
.orderStatus.active{
    background: #26A541;
}
.orderStatus:last-child{
    width: 0;
}
.orderStatus .point{
    width: 10px;
    height: 10px;
    position: absolute;
    border-radius: 5px;
    background-color: #777;
    top: -3px;
}
.orderStatus .point.active{
    background-color: #26A541;
}
.orderInfo{
    height: 60px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    top: -26px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

.delItemName{
    font-size: 12px;
}

.orderInfo .status{
    color: #26A541;
    text-transform: capitalize;
    font-weight: bold;
}

.orderInfo .date{
    font-size: 10px;
}
